import { Plant } from '../types';

const STORAGE_KEY = 'quiet_garden_data';

export interface StorageData {
  plants: Plant[];
  settings: {
    maxSeconds: number;
    maxDecibel: number;
    alertThreshold: number;
    seedlingsToTree: number;  // Êñ∞Â¢ûÔºöÂ§öÂ∞ë‰∏™Ê†ëËãóÂèòÊàêÊ†ë
    treesToFlower: number;    // Êñ∞Â¢ûÔºöÂ§öÂ∞ëÊ£µÊ†ëÂèòÊàêËä±
  };
}

export const defaultSettings: StorageData['settings'] = {
  maxSeconds: 5,
  maxDecibel: 20,
  alertThreshold: 15,
  seedlingsToTree: 5,     // ÈªòËÆ§5‰∏™Ê†ëËãóÂèòÊàê‰∏ÄÊ£µÊ†ë
  treesToFlower: 5        // ÈªòËÆ§5Ê£µÊ†ëÂèòÊàê‰∏ÄÊúµËä±
};

export const storageService = {
  getData: (): StorageData => {
    const data = localStorage.getItem(STORAGE_KEY);
    if (!data) {
      return { plants: [], settings: defaultSettings };
    }
    return JSON.parse(data);
  },

  saveData: (data: StorageData): void => {
    localStorage.setItem(STORAGE_KEY, JSON.stringify(data));
  },

  resetData: (): void => {
    localStorage.setItem(STORAGE_KEY, JSON.stringify({ plants: [], settings: defaultSettings }));
  },
};